
$topbarHeight: '38px';

.row {
  margin: 0 !important;
}

.v-dialog {
  /* v-dialog樣式寫在這裡。 */
}
.v-app-bar .dropdown-btn{
  padding: 0 10px;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.login-alert{
  position: fixed;
  top: 50px;  
  z-index: 10000;
  left: 0;
  right: 0;
  width: 300px;
  margin: auto;
}
.login-alert .v-alert__content{
  margin-left: -40px;
}
.font-super-small {
  font-size: 10px !important;
}

.font-small {
  font-size: 12px !important;
}

.font-normal {
  font-size: 14px !important;
}

.font-large {
  font-size: 16px !important;
}

.font-super-large {
  font-size: 18px !important;
}
// 桌機版
// @media (min-width: 1280px) and (max-width: 9999px) {}

// 平板版
// @media (min-width: 731px) and (max-width: 1279px) {}

// 手機板
@media (max-width: 730px) {
  ::-webkit-scrollbar {
    width: 2px;
  }
  .v-app-bar {
    height: #{$topbarHeight} !important;
    .v-slide-group, .v-toolbar__content, .v-toolbar__extension {
      height: #{$topbarHeight} !important;
    }
    .main-logo {
      width: 100px;
      height: 21px !important;
      padding: 0 !important;
    }
  }
}
